#page-container.pokemons-list {
  -ms-flex-align: center;
      align-items: center;
}

#pokemon-search-bar-wrapper {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background-color: var(--main-color);
  display: -ms-flexbox;
  display: flex;
  padding: 10px;
  -webkit-transition: 0.3s ease;
  -o-transition: 0.3s ease;
  transition: 0.3s ease;
  z-index: 2;
}

#pokemon-search-bar {
  font-family: "Bungee";
  -ms-flex: 1 1;
      flex: 1 1;
  font-size: 1rem;
  border: 0;
  border-radius: 5px;
  height: 50px;
  padding-left: 20px;
}

@media all and (min-width: 576px) {
  #page-container.pokemons-list {
    -ms-flex-align: start;
        align-items: start;
  }
}



